@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-image: url(/static/media/hooper-city.565da2a9.jpg);
  background-size: cover;
}

body{
  font-family: Georgia, 'Times New Roman', Times, serif;
}

.header {
  background: whitesmoke;
  display: flex; 
  width: 100vw;
  height: 11vh;
  position: fixed;
  top: 0;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0px 0px 20px 0px rgba(34, 34, 34, 0.1);
}


.logo {
  height: 8.5vh;
  margin: 10px;
}

.overlay{
  width: 100vw;
  background: rgba(0, 0, 0, 0.5);
}


.content{
  display: flex;
  justify-content: center;
  margin-top: 3%;
  margin: 3%;
  min-height: 80vh;
  width: 90vw;
  background: rgba(255, 255, 255, 0.75);
  border-radius: 6px;
  box-shadow: 0px 0px 12px 2px rgba(0, 0, 0,.5);
}

.container {
  margin-top: 11vh;
  min-height: 89vh;
  width: 100vw;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}
.home-content{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.home-content h1{
    color: rgb(24, 133, 24);
}

.content-container{
    flex-wrap: wrap;
    display: flex;
    justify-content: center; 
    align-items: center;
}

.sprinkler-image{
    margin: 5px;
    width: 45%;
    min-width: 250px;
    box-shadow: 1px 1px 15px 0px rgba(29, 29, 29, 0.4);
    border-radius: 5px;
}

.intro{
    width: 85%;
    margin: 50px 15px 15px 15px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 2vw;
}

.status{
    font-style: italic;
    width: 85%;
    margin: 15px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 1.8vw;
}
.contact{
    margin: 30px;
    align-items: center;
    min-width: 300px;
    width: 75%;
}

.home-text{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50%;
}

#contact-us{
    margin: 3px;
    text-decoration: underline;
    color: rgb(42, 42, 42);

}

.subheader{
    margin: 15px 3px 5px 0px;
    text-align: left;
    color: rgb(51, 51, 51);
    font-size: 18px;
}

.subheader:first-of-type{
    margin: 5px 0px;
}

.hours{
    display: flex;
    justify-content: space-between;
    width: 90%;
}

.left-align{
    font-size: 1em;
    text-align: left;
    margin: 4px 2px;
}

@media screen and (max-width: 672px){
    .home-text{
        width: 90%;
    }
    .intro{
        font-size: 18px;
        margin: 10px;
    }
}

@media screen and (max-width: 815px){
    .intro{
        font-size: 3vw;
    }
}
.update-notice{
    color: maroon;
}
.schedule-table{
    min-width: 270px;
    max-width: 500px;
    width: 45%;
    overflow: auto;
    border-radius: 5px;
    border-collapse: collapse;
    border: 1px solid black;
    margin-bottom: 25px;
}

.schedule-table tr:not(:nth-child(1)):hover{
    background: rgba(0, 135, 252, 0.2);
}
.schedule-table td{
    border: 1px solid black;
    padding: 5px;
}


.schedule-table th{
    border: 1px solid black;
    border-bottom: 2px solid black;
    padding: 7px;
}

.schedule-table td:first-child{
    text-align: left;
    padding-left: 8px;
}
.schedule-table td a{
    text-decoration: none;
    color: #1A0DAB

}
.grid-display {
    width: 95%;
    display: grid;
}
#schedules-content{
    flex-direction: column;
    align-items: center;
}
.about-content{
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
p{
    margin: 5px;
}

.person{
    display: flex;
    flex-direction: column;
    width: 80%;
    text-align: left;
    margin: 10px;
}

.person h4{
    margin: 5px;
}

#bylaws-content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.bylaws-link{
    margin: 10px;
    font-size: 18px;
}

.bylaws-info{
    width: 80%;
}

.bylaws-links{
    display: flex;
}

.bylaws-title{
    margin: 15px;
}
#specs-content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.spec-link{
    margin: 10px;
    font-size: 18px;
}

.spec-info{
    width: 80%;
}

.spec-links{
    display: flex;
}

.spec-title{
    margin: 15px;
}

#service-content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.service-link{
    margin: 10px;
    font-size: 18px;
}

.service-info{
    width: 80%;
}

.service-links{
    display: flex;
}

.service-title{
    margin: 15px;
}

.Footer {
    height: 5vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    color: darkgrey;
}

@media (max-width: 550px){
    .Footer {
        font-size: 10px;
    }
}
.top-nav{
    height: 11vh;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.top-nav .nav-links{
  height: 100%;
  display: flex; 
  justify-content: center;
  align-items: center;
}
.top-nav .nav-links .link{
    display: flex;
    justify-content: center;
    align-items: center;
    float: left;
    height: 100%;
    padding: 0 9px;
    color: black;
    text-decoration: none;
    font-size: 20px;
    font-weight: 600;
}

.top-nav .drop-icon {
    display: none;
    position: absolute;
    top: 3vh;
    align-items: center;
    font-size: 25px;
}

.dropdown {

    float: left;
    overflow: hidden;
    height: 100%;
}

.dropdown .dropdown-content .link{
  height: 40px;
}

 .dropdown .dropbtn {
    height: 100%;
    font-size: 20px;    
    border: none;
    outline: none;
    color: black;
    background-color: inherit;
    font-family: inherit;
    padding: 0 9px;
    font-weight: 600;
  }


  .dropdown .dropbtn:hover {
    background: rgba(0,0,0,0.2)
   }

  i {
    border: solid black;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
    margin: 0 0 3px 3px;
  }
  
  .down {
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }


  .dropdown-content {
    display: none;
    position: absolute;
    background-color: whitesmoke;
    min-width: 160px;
    box-shadow: 0px 8px 8px 0px rgba(0,0,0,0.1);
    z-index: 1;
    right: 25px;
    border-radius: 0 0 5px 5px;
  }
  
  .dropdown-content .link {
    float: none;
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
    border-bottom: 1px solid black;
  }

  .dropdown-content .link:last-child {
    border: none;
  }

  .dropdown:hover .dropdown-content {
    display: flex;
    flex-direction: column;
  }

  .link:hover {
      height: 100%;
      background: rgba(0,0,0,0.2)
  }

  @media screen and (max-width: 680px) {
    .top-nav .nav-links{
      display: none;
    }
    .top-nav .drop-icon {
      display: flex;
    }
    .top-nav {
      position: absolute;
      right: 0;
    }
  }
  #hamburger-icon{
    padding-right: 10px;
  }
  
  @media screen and (max-width: 680px) {
    .top-nav.responsive{
      display: flex;
    }
    .top-nav.responsive .nav-links{
      height: auto;
      width: 100vw;
      display: flex;
      flex-direction: column;
      position: fixed;
      top: 11vh;
      right: 0;
      background: white;
    }
    .top-nav.responsive .nav-links .link{
      position: relative;
      border-top: 1px solid black;
      width: 100vw;
      padding: 7px;
      height: auto;
    }

    .top-nav.responsive .nav-links .dropbtn{
      padding: 7px;
      width: 100vw;
      height: auto;
      border-top: 1px solid black;
      height: auto;
    }
    .top-nav.responsive .dropdown-content{
      position: relative;
      right: 0;
      width: 100vw;
    }
    .top-nav.responsive .nav-links .dropdown .dropdown-content .link{
      border-bottom: none;
      box-shadow: none;
      border-top: 1px solid black;
    }
  }
